<template>
  <div class="common-detail">
    <div class="content">
      <Form :label-width="120">
          <FormItem label="标题：">
            <label>{{watchTableCell.val1}}</label>
          </FormItem>
          <FormItem label="是否公开：">
            <label>{{watchTableCell.val2?'是':'否'}}</label>
          </FormItem>
          <FormItem label="编辑内容：">
            <label>
              <!-- {{watchTableCell.val3}} -->
                   <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="watchTableCell.val3"
                  >
                    <p slot="reference" class="textMaxWidth2 disBlock textOverflow" >{{watchTableCell.val3}}</p>
                  </el-popover>
              </label>
          </FormItem>
          <FormItem label="附件：">
            <showUsualFiles
              :file-obj="dataObj4"
              :key="fileId4"
            ></showUsualFiles>
          </FormItem>
          <FormItem label="追加附件：">
            <showUsualFiles
              :file-obj="dataObj5"
              :key="fileId5"
            ></showUsualFiles>
          </FormItem>
          <FormItem label="追加内容：">
            <label>
              <!-- {{watchTableCell.val11}} -->
                  <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="watchTableCell.val11"
                  >
                    <p slot="reference" class="textMaxWidth2 disBlock textOverflow" >{{watchTableCell.val11}}</p>
                  </el-popover>
              </label>
          </FormItem>
        </Form>
    </div>
  </div>
</template>
<script>
import showUsualFiles from "@/components/commonComp/showUsualFiles";
export default {
  props: ["dataObj"],
  components: {
    showUsualFiles
  },
  data() {
    return {
      addPeopleArr: [], //被调查人
      watchTableCell: {
        val1: "",
        val2: "",
        val3: "",
        val4: "",
        val5: "",
        val6: "",
        val7: "",
        val8: "",
        val9: "",
        val10: "",
        val11: ""
      },
      fileId4: "",
      fileId5: "",
      dataObj4: {},
      dataObj5: {}
    };
  },
  created() {
    let data = this.dataObj.data;
    if (data) {
      this.watchTableCellFun(data, data.busId, data.id);
    }
  },
  methods: {
    watchTableCellFun(val1, val2, val3) {
      this.axios
        .post(`/process/infPublicOpinionMonitoring/getEntityRow/${val3}`+'?infViewNum=1')
        .then(response => {
          if (response.code == "200") {
            this.watchTableCell.val1 = response.data.title;
            this.watchTableCell.val2 = JSON.parse(response.data.ifShow);
            this.watchTableCell.val3 = response.data.content;
            this.dataObj4 = {
              data: response.data,
              showFlag: false,
              disabled: false
            };
            this.fileId4 = response.data.busId;
          }
        })
        .catch(function(error) {});
      // -----------------
      let data2 = {
        relationBusId: val2
      };
      data2 = JSON.stringify(data2);
      this.axios
        .post(`/process/infProcessAppend/getInfProcessAppendRows`, data2, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.appendId = response.data.id;
            this.watchTableCell.val11 = response.data.content;
            this.dataObj5 = {
              data: response.data,
              showFlag: false,
              disabled: false
            };
            this.fileId5 = !this.fileId5;
          }
        })
        .catch(function(error) {});
      // ---------------
      this.watchTableCellDitalModel = true;
    },
  }
};
</script>


